import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes,RouterModule } from '@angular/router';
import { CustomReportListComponent } from './custom-report-list/custom-report-list.component';
import {CustomReportEditComponent} from "./custom-report-edit/custom-report-edit.component";
import { CustomReportNewComponent } from './custom-report-new/custom-report-new.component';

const manageReportRoutes: Routes = [
    {path: '',component: CustomReportListComponent}
    ,{path: 'new', component: CustomReportNewComponent}
    ,{path: ':reportId/edit', component: CustomReportEditComponent}
  ];
@NgModule({
  imports: [
    RouterModule.forChild(manageReportRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class ManageReportsRoutingModule {}
