import { Component, OnInit, EventEmitter, Output, Input, ElementRef, ViewChild, ComponentFactoryResolver, ViewContainerRef, ComponentRef } from '@angular/core';
import { AlertService } from '../../shared/services/alert.service';
import { IReportItemEditorComponent } from '../models/i-report-item-editor-component';
import { ItemJsonEditorComponent } from '../item-json-editor/item-json-editor.component';
import { ItemHtmlContentEditorComponent } from '../item-html-content-editor/item-html-content-editor.component';
import { ItemTherapistScaleResultsEditorComponent } from '../item-therapist-scale-results-editor/item-therapist-scale-results-editor.component';
import { ItemScaleResultsSummaryEditorComponent } from '../item-scale-results-summary-editor/item-scale-results-summary-editor.component';

@Component({
  selector: 'dac-report-item-edit',
  templateUrl: './report-item-edit.component.html',
  styleUrls: ['./report-item-edit.component.scss']
})
export class ReportItemEditComponent implements OnInit {

  private itemEditor:ComponentRef<IReportItemEditorComponent>;

  @ViewChild('editorContainer', { read: ViewContainerRef }) editorContainer;

  @Input() item:any;
  @Input() report:any;
  @Input() first:boolean;
  @Input() last:boolean;
  public editing:boolean;

  @Output() update:EventEmitter<any>;
  @Output() delete:EventEmitter<any>;
  @Output() moveUp:EventEmitter<any>;
  @Output() moveDown:EventEmitter<any>;

  constructor(private alertService:AlertService, private resolver: ComponentFactoryResolver) {
    this.editing = false;
    this.update = new EventEmitter();
    this.delete = new EventEmitter();
    this.moveUp = new EventEmitter();
    this.moveDown = new EventEmitter();
  }

  ngOnInit() {
    // we init this as some reports don't have this value
    // TODO: stephen at some point we want to remove this as every report item will have this saved.
    if (this.item.display !== false) {
      this.item.display = true;
    }
  }


  createComponent(type, data) : ComponentRef<IReportItemEditorComponent> {
    let component = this.getComponentFromType(type);
    let factory = this.resolver.resolveComponentFactory(component);
    let componentRef = this.editorContainer.createComponent(factory);
    let reportItemComponent = componentRef.instance as IReportItemEditorComponent;
    reportItemComponent.setEditorData(data);
    return componentRef;
  }

  private getComponentFromType(type:string) {
    var component = null;
    switch (type)
    {
      case 'TherapistScaleResults': {
        component = ItemTherapistScaleResultsEditorComponent;
      }
      break;
      case 'ClientScaleResults': {
        component = ItemTherapistScaleResultsEditorComponent;
      }
      break;
      case 'AddoScaleResults': {
        component = ItemTherapistScaleResultsEditorComponent;
      }
      break;
      case 'HTMLContentItem': {
        component = ItemHtmlContentEditorComponent;
      }
      break;
      // case 'ScaleResultsSummary': {
      //   component = ItemScaleResultsSummaryEditorComponent;
      // }
      // break;
      default: {
        component = ItemJsonEditorComponent;
      }
      break;
    }
    return component;
  }


  public toggleEdit() {
    if (!this.editing) {
      this.itemEditor = this.createComponent(this.item.type, this.item.data);
    }
    else {
      this.itemEditor.destroy();
    }
    this.editing = !this.editing;
  }

  public notifyDelete() {
    this.delete.emit(this.item);
  }

  public notifyMoveUp() {
    this.moveUp.emit(this.item);
  }

  public notifyMoveDown() {
    this.moveDown.emit(this.item);
  }

  public toggleDisplay(item) {
    if (item.display === false) {
      item.display = true;
    }
    else {
      item.display = false;
    }
  }

  public get Report() {
    return this.report || {};
  }

  public notifyUpdate() {
    let data = this.itemEditor.instance.getEditorData().then((data) => {
      this.item.data = data;
      this.update.emit(this.item);
    })
    .catch((error) => {
      console.error("Save error occurred.  Detailed error from editor item: ", error);
    });
  }
}
