import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { IReportItemEditorComponent } from '../models/i-report-item-editor-component';
import { AlertService } from '../../shared/services/alert.service';

@Component({
  selector: 'dac-item-html-content-editor',
  templateUrl: './item-html-content-editor.component.html',
  styleUrls: ['./item-html-content-editor.component.scss']
})
export class ItemHtmlContentEditorComponent implements OnInit, IReportItemEditorComponent {

  public newClassName:string;

  public content:string;
  public classNames:string[];
  @ViewChild("contentEditor") contentEditor:ElementRef;
  @ViewChild("newClass") newClass:ElementRef;

  constructor(private alertService:AlertService) { }

  ngOnInit() {
    this.newClassName = "";
  }

  public addClassName() {
    let className = this.newClassName.trim();
    if (className == "") {
      this.alertService.error("Please enter a class name to continue");
      return;
    }

    this.classNames.push(className);
    this.newClassName = "";
  }
  public removeClass(name:string) {
    this.classNames = this.classNames.filter(cn => cn != name);
  }

  setEditorData(editorData: any) {
    let data = editorData || {};
    this.classNames = data.classNames || [];
    this.content = data.content || "";
  }
  getEditorData(): Promise<any> {
    let editor = this.contentEditor.nativeElement as HTMLTextAreaElement;
    let content = editor.value.trim();

    let data = {
      classNames: this.classNames
      ,content: content
    };
    return Promise.resolve(data);

  }
}
