import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { IReportItemEditorComponent } from '../models/i-report-item-editor-component';
import { AlertService } from '../../shared/services/alert.service';

@Component({
  selector: 'dac-item-json-editor',
  templateUrl: './item-json-editor.component.html',
  styleUrls: ['./item-json-editor.component.scss']
})
export class ItemJsonEditorComponent implements OnInit, IReportItemEditorComponent {
  public data:any;
  @ViewChild("itemEditor") itemEditor:ElementRef;

  constructor(private alertService:AlertService) { }

  ngOnInit() {
  }


  setEditorData(data: any) {
    this.data = data;
  }
  getEditorData(): Promise<any> {
    let text = this.itemEditor.nativeElement as HTMLTextAreaElement;
    let json = text.value;
    try {
      let data = JSON.parse(json);
      return Promise.resolve(data);
    }
    catch (error) {
      console.error(error);
      this.alertService.error("There is an error in your JSON");
      return Promise.reject(error);
    }
  }

}
