import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'dac-item-scale-results-summary-editor',
  templateUrl: './item-scale-results-summary-editor.component.html',
  styleUrls: ['./item-scale-results-summary-editor.component.scss']
})
export class ItemScaleResultsSummaryEditorComponent implements OnInit {

  public title:string;
  public description:string;
  public sections:any[];

  constructor() { }

  ngOnInit() {
  }
  setEditorData(editorData: any) {
    let data = editorData || {};
    this.title = data.title || "";
    this.description = data.description || "";
    this.sections = data.sections || [];
  }
  getEditorData(): Promise<any> {

    return Promise.resolve({
      title: this.title
      ,description: this.description
      ,sections: this.sections
    });

  }


}
